@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,500;0,700;0,900;1,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  align-items: center;
  background-image: linear-gradient(rgb(236, 236, 17), rgb(228, 34, 34) 80%);
  display: flex;
  height: 100vh;
  justify-content: center;
}

body {
  display: flex;
  font-family: Roboto , sans-serif;
  height: 640px;
  margin: auto;
  overflow: scroll;
  width: 360px;
}

.root {
  width: 100%;
}


.detail-image {
  max-width: 80%;
}

* {
  margin: 0;
}

* {
  margin: 0;
}

.header-container {
  background-color: #e1e1e1;
  display: flex;
  justify-content: space-between;
  padding: 10px 15px;
  width: 100%;
}

.header-container button {
  border: none;
}


.search-container {
  padding: 10px;
}

.input-search {
  padding: 5px 10px;
  width: 100%;
}

.search-container label {
  margin: 2px 0 0 10px;
}

.card-container {
  align-items: center;
  background-color: white;
  border-radius: 10px;
  box-shadow: 3px 3px 5px 2.7px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  float: left;
  margin-bottom: 30px;
  padding: 10px 0 15px;
  width: 80%;
}

.card-container img {
  border-radius: 10px;
  width: 80%;
}

.carousel-card-container {
  padding-left: 20px;
  width: 50%;
}

.carousel-card-container img {
  width: 100%;
}

.footer {
  background-color: #e1e1e1;
  bottom: 0;
  clear: both;
  display: flex;
  justify-content: space-between;
  max-width: 360px;
  padding: 10px 15px;
  position: fixed;
  width: 100%;
}

input:hover, label {
  cursor: pointer;
}

input:checked + label {
  text-decoration: line-through;
}

ul {
  list-style-type: none;
}

.ol-styled {
  display: flex;
  flex-flow: column wrap;
  list-style-position: inside;
  text-align: center;
}

.button-container {
  display: flex;
}

.button-container > button {
  margin: 2px;
  padding: 5px;
}

