  padding-right: 8px;
} */

.ingredient-card-container {
  align-items: center;
  background-color: white;
  border-radius: 10px;
  box-shadow: 3px 3px 5px 2.7px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  float: left;
  justify-content: space-around;
  margin-bottom: 10px;
  margin-top: 5px;
  padding: 10px 0 15px;
  text-align: center;
  width: 35%;
}

.ingredient-card-container img {
  border-radius: 10px;
  width: 40%;
}

.ingredient-card-complete-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  width: auto;
}

* {
  margin: 0;
}

.cards {
  align-items: center;
  background-color: var(--primary-bg-color);
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
  margin-top: 15px;
}

.spacing {
  min-height: 30px;
}

* {
  margin: 0;
}

.carousel {
  align-items: center;
  display: flex;
  flex-direction: row;
  overflow-x: scroll;
  width: 300%;
}

.carousel-wraper {
  margin: auto;
  margin-bottom: 200px;
  overflow-x: scroll;
  width: 350px;
}

.icons {
  display: flex;
  justify-content: space-around;
  padding: 10px 0;
  width: 100%;
}

.recipe-container {
  align-items: center;
  display: flex;
  flex-direction: column;
}

.detail-image {
  border-radius: 10px;
}

.start-btn {
  background-color: rgb(21, 44, 177);
  border: none;
  bottom: 0;
  color: white;
  font-weight: 900;
  opacity: 0.9;
  padding: 15px 0;
  position: fixed;
  width: 300px;

}

.start-btn:hover {
  opacity: 1;
}

.share-btn, .favorite-btn {
  background: none;
  border: none;
  outline: none;
  transform: scale(1);
  transition: all 0.2s ease;
}

.share-btn:focus, .favorite-btn:focus {
  background: none;
  outline: none;
}

.share-btn:hover, .favorite-btn:hover {
  transform: scale(1.15);
}

:root {

  --button-disabled-color: rgb(239, 239, 243);

  --button-disabled-font-color: rgb(223, 219, 219);

  --button-bg-color: rgb(228, 26, 26);

  --button-click-color: rgb(248, 182, 182);

  --button-hover-color: rgb(255, 62, 62);

  --label-font-color: rgb(228, 26, 26);

  --input-basic-color: rgba(246, 158, 176, 0.5);

  --input-focus-color: rgba(243, 56, 94, 0.459);

  --input-bg-color: rgb(228, 222, 222);

  --input-font-color: rgb(100, 100, 100);

  --input-placeholder-color: rgb(139, 137, 137);

  --font-francois: 'Francois One' , sans-serif;

  --font-patrick: 'Patrick Hand' , cursive;

  --font-radjhani: 'Rajdhani' , sans-serif;

  --font-satisfy: 'Satisfy' , cursive;

  --font-sigmar: 'Sigmar One' , cursive;

  --font-nunito: 'Nunito' , sans-serif;

  --primary-bg-color: rgba(250, 244, 244, 0.363);

  --primary-shadow-color: rgb(245, 238, 238);

  --primary-font-size: 16px;

}

.login-container {
  align-items: center;
  background-color: rgba(250, 244, 244, 0.363);
  background-color: var(--primary-bg-color);
  display: flex;
  flex-direction: column;
  height: 640px;
  justify-content: space-between;
  margin: auto;
  width: 360px;
}

.login-container img {
  width: 60%;
}

.login-img-container {
  align-items: center;
  background-color: white;
  border-radius: 0.01px 0.01px 40px 40px;
  box-shadow: 0.1px 10px 25px 10px rgb(245, 238, 238);
  box-shadow: 0.1px 10px 25px 10px var(--primary-shadow-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 15px;
  width: 360px;
}

.form-box {
  align-items: center;
  display: flex;
  flex-direction: column;
  margin-bottom: 50px;
}

.login-input {
  background-color: rgb(228, 222, 222);
  background-color: var(--input-bg-color);
  border: 1px solid rgb(228, 222, 222);
  border: 1px solid var(--input-bg-color);
  border-radius: 7px;
  color: rgb(100, 100, 100);
  color: var(--input-font-color);
  margin: 6px 0.1px;
  outline: none;
  padding: 2px 5px;
  transition: 200ms;
}

.login-input:focus {
  border-color: rgba(243, 56, 94, 0.459);
  border-color: var(--input-focus-color);
  box-shadow: 0.1px 0.1px 3px 3px rgba(246, 158, 176, 0.5);
  box-shadow: 0.1px 0.1px 3px 3px var(--input-basic-color);
}

.input-label {
  color: rgb(228, 26, 26);
  color: var(--label-font-color);
  display: flex;
  flex-direction: column;
  font-family: 'Nunito' , sans-serif;
  font-family: var(--font-nunito);
  font-size: 16px;
  font-size: var(--primary-font-size);
  font-weight: 600;
  width: 300px;
}

.input-login-button {
  background-color: rgb(228, 26, 26);
  background-color: var(--button-bg-color);
  border: 2px solid rgb(228, 26, 26);
  border: 2px solid var(--button-bg-color);
  border-radius: 12px;
  color: rgb(228, 222, 222);
  color: var(--input-bg-color);
  font-family: 'Nunito' , sans-serif;
  font-family: var(--font-nunito);
  font-weight: 500;
  margin: 10px;
  outline: none;
  padding: 4px 10px;
  transition: 200ms;
  width: 180px;
}

.input-login-button:hover {
  background-color: rgb(255, 62, 62);
  background-color: var(--button-hover-color);
  box-shadow: 0.1px 0.1px 3px 3px rgba(246, 158, 176, 0.5);
  box-shadow: 0.1px 0.1px 3px 3px var(--input-basic-color);
}

.input-login-button:disabled {
  background-color: rgb(239, 239, 243);
  background-color: var(--button-disabled-color);
  border: 2px solid rgb(239, 239, 243);
  border: 2px solid var(--button-disabled-color);
  color: rgb(223, 219, 219);
  color: var(--button-disabled-font-color);
}

.input-login-button:active {
  background-color: rgb(248, 182, 182);
  background-color: var(--button-click-color);
  border: rgb(248, 182, 182);
  border: var(--button-click-color);
  outline: none;
}

.center-items-div {
  align-items: center;
  display: flex;
  font-family: 'Satisfy' , cursive;
  font-family: var(--font-satisfy);
  font-size: 2.5rem;
  height: 40px;
  justify-content: center;
  margin-top: 25px;
  width: 360px;
}

.filter-btn {
  box-sizing: 'content-box';
  margin: '10px';
  padding: '5px';
}

.cardFav-container {
  box-shadow: 1px 2px 8px #aaa;
  display: flex;
  margin: 20px;
}

.cardFav-container .imagem-fav {
  width: 165px;
}

.cardFav-content {
  display: flex;
  flex-direction: column;
  padding: 10px;
  width: 100%;
}

.cardFav-content p {
  color: #aaa;
}

.cardFav-content h4 {
  margin: -15px 0 0;
}

.iconFav {
  display: flex;
  justify-content: space-around;
}

.icon-fav {
  width: 40px;
}

